.header {
  background: #25263a;
}
.headerContainer {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
}
.menuList {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.menuLink {
  display: block;
  font-size: 20px;
  color: #ffffff;
  text-decoration: none;
  padding: 10px;
  transition: .3s;
  margin-right: 30px;
  cursor: pointer;
}
.menuLink:hover {
  color: #24C5DB;
}
.active {
  color: #24C5DB;
}